.wrapper {
    padding: 0px 20px;
    max-width: 800px;

    margin: 0px auto;
}

.title {
    width: 100%;
    text-align: center;
}

.input-wrapper {
    width: calc(100% - 10px);
    height: 24px;
    border: 1px solid #cdcdcd;
    padding: 4px;
    border-radius: 4px;

    &:focus {
        outline: none;
    }
}

.suggestion-wrapper {
    height: 40px;
    background-color: #f2f2f2;
    margin-top: 40px;
    display: flex;
    column-gap: 28px;
    justify-content: center;
    align-items: center;
}

.suggestion-wrapper>div {
    background-color: rgba(0, 0, 0, 0.9);
    color: #fff;

    height: fit-content;
    width: fit-content;
    padding: 4px 10px;
    border-radius: 4px;
    min-width: 28px;
    text-align: center;
    font-weight: 500;
    font-size: 14px;
    cursor: pointer;
}

.model-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 12px;
    padding: 12px;

    background-color: rgb(252 255 243);
    border: 1px solid #cdcdcd;
    border-radius: 6px;
    margin-bottom: 28px;
}

.model-title {
    font-size: 15px;
    margin: 0px;
}

.model-list-wrapper {
    display: flex;
    column-gap: 12px;
}

.model-list-wrapper>div {
    border: 1px solid #000;
    border-radius: 4px;
    padding: 6px 12px;
    font-size: 12px;
    font-weight: 500;
    cursor: pointer;

    transition: all 0.25s ease-in-out;
}

.model-list-wrapper .selected {
    background-color: #000;
    color: #fff;
}

.watermark {
    position: absolute;
    bottom: 10px;
    right: 10px;
    font-size: 10px;
    color: black;
    opacity: 0.7;
}